<template>
    <div>
        <v-row>
            <v-col v-if="loading">
                <v-card class="mx-auto" max-width="800px">
                    <v-skeleton-loader type="card"/>
                    <div v-if="showTypeAndTags">
                        <div class="d-flex">
                            <v-icon class="pa-2">mdi-view-list</v-icon>
                            <v-skeleton-loader class="mt-1 mb-1" type="button"/>
                        </div>
                        <div class="d-flex">
                            <v-icon class="pa-2">mdi-tag-multiple</v-icon>
                            <v-skeleton-loader class="mt-1 mb-1" type="chip"/>
                        </div>
                    </div>
                    <v-skeleton-loader type="list-item-three-line, list-item"/>
                </v-card>
            </v-col>
            <v-col v-else v-for="item in blogArticles" :key="item.id">
                <v-card @click="clickArticle(item)" hover class="mx-auto" max-width="800px">
                    <v-img class="white--text align-end" :max-height="$vuetify.breakpoint.smAndDown?'200px':'300px'"
                           :src="item.imgUrl===null?'/assets/header.jpg':item.imgUrl">
                        <v-card-title>{{item.title}}</v-card-title>
                    </v-img>
                    <div v-if="showTypeAndTags">
                        <div v-if="item.typeString!==undefined && item.typeString!==null">
                            <div class="d-inline">
                                <v-icon class="pa-2">mdi-view-list</v-icon>
                                <v-btn disabled text>
                                    {{item.typeString}}
                                </v-btn>
                            </div>
                        </div>
                        <v-divider/>
                        <div v-if="item.tags!==undefined && item.tags.length">
                            <div class="d-flex">
                                <v-icon class="pa-2">mdi-tag-multiple</v-icon>
                                <v-chip-group column>
                                    <v-chip disabled v-for="tag in item.tags" :key="tag">
                                        {{tag}}
                                    </v-chip>
                                </v-chip-group>
                            </div>
                        </div>
                        <v-divider/>
                    </div>
                    <v-card-text>{{item.articleAbstract}}</v-card-text>
                    <v-divider/>
                    <v-col class="text--darken-4">
                        <v-icon small>mdi-eye-outline</v-icon>
                        {{item.clickCount}}
                        <v-icon small class="ml-2">mdi-chat-outline</v-icon>
                        {{item.id}}
                        <v-icon small class="ml-2">mdi-clock-time-four-outline</v-icon>
                        {{item.updateTimeString}}
                    </v-col>
                </v-card>
            </v-col>
        </v-row>
        <div class="text-center">
            <v-pagination v-if="blogArticles.length>0" v-model="page" :length="totalPage"/>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ArticlePreview",
        props: ['blogArticles', "totalPage", "loading", "showTypeAndTags"],
        data() {
            return {
                page: 1
            }
        },
        methods: {
            clickArticle: function (blogArticle) {
                //path不能和params连用
                this.$router.push({
                    name: "article",
                    params: {id: blogArticle.id, articleFromPre: blogArticle}
                })
            }
        },
        watch: {
            page: function () {
                this.$emit("transferCurrentPage", this.page)
                this.$vuetify.goTo(0, {duration: 0})
            }
        },
        created: function () {
        }
    }
</script>

<style scoped>

</style>